export function handleBinValue(pFeedbackObj) {
    var binValue = pFeedbackObj.binValue, encryptedBin = pFeedbackObj.encryptedBin, uuid = pFeedbackObj.uuid;
    var callbacksObj = { binValue: binValue, type: this.state.type };
    if (encryptedBin) {
        callbacksObj.encryptedBin = encryptedBin;
        callbacksObj.uuid = uuid;
    }
    this.callbacks.onBinValue(callbacksObj);
}
