import { h } from 'preact';
import useCoreContext from '../../../core/Context/useCoreContext';
export default function SignOutButton(props) {
    var i18n = useCoreContext().i18n;
    var handleClick = function () {
        new Promise(props.onSignOut)
            .then(function () {
            props.amazonRef.Pay.signout();
        })
            .catch(console.error);
    };
    return (h("button", { type: "button", className: "adyen-checkout__button  adyen-checkout__button--ghost adyen-checkout__amazonpay__button--signOut", onClick: handleClick }, i18n.get('amazonpay.signout')));
}
