export var boletoValidationRules = {
    input: {
        socialSecurityNumber: function (ssn) { return /(^\d{3}\.\d{3}\.\d{3}-\d{2}$)|(^\d{2}\.\d{3}\.\d{3}\/\d{4}-\d{2}$)/.test(ssn); }
    },
    blur: {
        socialSecurityNumber: function (ssn) { return /(^\d{3}\.\d{3}\.\d{3}-\d{2}$)|(^\d{2}\.\d{3}\.\d{3}\/\d{4}-\d{2}$)/.test(ssn); },
        default: function (value) { return value && value.length > 0; }
    }
};
